/* Navbar CSS */

img {
  width: 100%;
  height: 75px;
}

.custom-dropdown-menu {
  background-color: #2b2b2b;
}

.navbar {
  height: 100px;
  background-color: #2b2b2b;
}

.navbar-brand {
  font-size: 32px;
  margin-left: 16px;
  margin-right: 24px;
  margin-bottom: 0.25%;
  color: white !important;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.nav-link {
  font-size: 20px;
  text-align: center;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.nav-dropdown{
  background-color: inherit;
}

.material-symbols-outlined {
  display: block !important;
}

#logout {
  right: 0;
}